<template>
	<!-- 新闻网首页最上方的滑动导航栏 -->
	<view class="home">
		<!-- uniapp封装好的滑动导航条标签<scroll-view>,scroll-x=true代表允许横向滑动 -->
		<!-- nav=navigation 导航的意思,scroll 滚动 -->
		<scroll-view scroll-x>
			<view class="navscroll">
				<!-- 难点4，html允许一个标签有多个class，例如class=style1 style2.相当于同时这么写class=style1 class=style2。 -->
				<!-- 难点5，竟然可以在class前面加上：即v-moudle，意思为将class的值双引号中的东西当成表达式看。再把该class的值写成条件运算符index==navIndex? 'active' : '',意思为·如果index==navIndex的值为true，那么就将active的值当作该条件表达式的值，反之‘’作为该条件表达式的值，那么最终就实现了用户点导航栏上哪个view，那么该view就变绿色 -->
				<!-- v-for=“（a,b) in c”中的c的值为数组时，数组长度是多少就创建多少个该view标签，c的值也可以是数字，意思为创建多少个view标签。a代表次数遍历的数组中的元素，b代表此时遍历的数组元素的索引值,并且每一个由v-for创建的<view>标签内部都有属性名为b，属性值为 数组a的索引值0/1/2 的键值对，该键值对的作用就是用于区分这些相同的《view》标签的唯一标识-->
				<view class="item" :class="index==navIndex? 'active' : '' " v-for="(item,index) in navArr"
					:key="item.id" @click="clickNav(index,item.id)">{{item.classname}}</view>
			</view>
		</scroll-view>



		<!-- 新闻网导航栏下方的内容展示区 -->
		<view class="content">
			<div class="row" v-for="item in newsArr" :key="item.id">
				<newsbox :item="item"></newsbox>
			</div>
		</view>

		<!-- 添加内容到底了的提示，文章列表数据有newsArr长度就不显示该提示 -->
		<view class="nodata" v-if="!newsArr.length">
			<div>内容到底了，去其他地方看看吧</div>
		</view>
		<!-- 需求1，当展示新闻列表被用户下拉到底时，还未获取到更多新闻内容时，出现“正在加载中”的提示，即显示该view标签-->
	    <!-- 难点十三 实现需求1.在onReachBotto生命周期方法中让loading的值变成1。并且当请求更多新闻列表数据的方法getNewData（）执行后请求到的数据为0时，即数组res.data里面没有元素。即数组长度为0时，让loading的值对于2，提示没有更多了。并且还要在用户点击导航栏上其他选项时初始化loading的值为0，即在clickNav方法里面操作。-->
		<!-- 还要注意当点击导航栏上的选项时获取到的新闻列表数据为空数组时，即发送请求后得到的newsArr的值是空数组,只出现“内容到底了，去其他地方看看吧”的提示，不出现下面这俩提示，就需要在下面这个view标签里面加上v-if="newsArr.length"-->
		<view class="loading" v-if="newsArr.length">
			<view v-if="loading==1">数据加载中...</view>
			<view v-if="loading==2">没有更多了...</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				navIndex: "1",
				// 存储从后端获取到的首页导航栏上的字的数据
				navArr: [],
				// 存储从后端获取到的新闻列表数据
				newsArr: [],
				// 存储向后端发送的请求中的参数currentPage的值。用于控制后端发送过来的第几页数据
				currentPage: 1,
				// 控制提示“正在加载中”、“没有更多了”的显示，默认值0代表什么提示都不显示
				loading:0  
			}
		},
		// 小程序的生命周期，该方法在页面加载时触发
		onLoad() {
			this.getNavData();
			this.getNewData()
		},
		// 需求2.难点十、十一。十二合计才实现用户下拉加载更多内容并且不影响其他导航栏展示的不同新闻列表的功能。
		// 难点十。用户下滑页面到当前展示的所有数据的底部时，就会触发该页面生命周期方法onreachbottom
		onReachBottom() {
			this.loading=1;
			// 触发该方法，向服务器获取更多数据，即向后端获取第二页数据，后端设置是向后端发送参数为pages=页树的形式。所以每次触发该方法时，让响应数据++，再触发一次向后端发送请求获取新闻列表内容的方法getNewData
			this.currentPage++;
			//难点搭配107行一起看。 直到下行代码中的方法在执行时请求的更多新闻列表数据为空时，即res.data的值是个空数组时，让loading的值为2，即出现提示“没有更多内容了”
			this.getNewData()
		},
		methods: {
			clickNav(index, id) {
				// this.navIndex=index;实现用户点击的导航栏上的选项的背景颜色变成绿色。猜测v-for中的index会作为创建的数个相同的view的区分标识，即每个由v-for创建的相同view标签里面都有index属性，并且该属性的值是索引即0，1，2，3，等等索引值
				// 用户点击导航栏中的某个view标签后（这些view标签都绑定了单击事件且响应函数都为这个clicknav），会将该view标签中的属性index的值赋值给data中的属性navindex，那么vue会重新编译所有有关navindex的模板、
				// 难点十二，用户点击导航栏中的某个view标签后，必须将this.navArr、this.currentPage都初始化变成值为【】、1.不然获取的其他类型的新闻列表数据都会组装在同一个数组中并一起展示，
				this.navIndex = index;
				this.newsArr = [];
				this.loading=0;
				this.currentPage = 1;
				this.getNewData(id)
			},
			// 页面加载时触发该方法获取首页导航栏数据
			getNavData() {
				uni.request({
					url: "https://ku.qingnian8.com/dataApi/news/navlist.php",
					// 请求成功后得到的响应数据会存到success方法中的形参res（该形参是个对象{}）中的data里面。
					success: res => {
						// 难点八。res.data这个数据里面有两个属性，分别是classname（导航栏上面的字）、id(用于传给后端该id的值来获取不同类型的新闻数据），所以需要把该属性id传给子组件newsbox，在子组件里面向后端发送该id获取不同类型文章数据并展示这些数据

						this.navArr = res.data;
						// console.log(res.data)
					}
				})
			},
			// 获取新闻列表数据
			//难点.从es6开始可以直接通过“形参=值”的形式来给函数中的形参设置默认值。
			getNewData(id = 5) {
				uni.request({
					url: "https://ku.qingnian8.com/dataApi/news/newslist.php",
					// data属性的值同样会发送给后端
					data: {
						// 难点9.后端接口设置num1属性的值为多少就返回多少条数据，cid的值为多少就只返回所有id为50的数据，从而达到了导航栏上点击不同选项（该不同选项后端设置了不同的id值，例如国内的id值就是50）获得不同类型数据的需求。
						num: 8,
						cid: id,
						// 向后端发送带有page的键值对参数的请求时，就会返回该类型为cid的新闻列表数据的第二页数据。
						page: this.currentPage
					},
					success: res => {
						// 难点。判断res.data的值是否为空数组，是就说明此时请求完所有新闻列表数据了，那么就改变loading的值为2，出现没有更多了的提示信息、
						if(res.data.length==0){
							this.loading=2;
						}
						// 难点十一，用户下拉到底部后获取新的新闻数据res.data时，要把旧的新闻列表数据（即当前this.newsArr的值）跟新获得的新闻列表数据拼接起来。...是es6新添加的扩展运算符，...【】相当于把数组中所有的元素都写出来，【】创建新数组的意思
						this.newsArr = [...this.newsArr, ...res.data];
						// console.log(res.data)
					}
				})
			}

		}
	}
</script>

// scope 范围领域,代表该style标签中的样式只适用于该组件中的template中的html代码
<style lang="scss">
	/* 直接给scroll-view标签加上flex布局是不会生效的，可以在该标签里面套一个view，给这个view开启flex布局 */
	.navscroll {
		/* 	height:250rpx; */
		background-color: #f7f8fa;
		display: flex;
		// 开启固定定位，让导航栏不会随着滚动条往下拉而消失。
		// 注意固定定位在浏览器中执行的时候，参照物原点是页面标题即绿色区域的左上角，所以top：0的效果是：页面绿色区域遮挡主了导航栏。
		// 而微信小程序里面运行时，由于页面最上方的绿色区域是微信系统自带的，不属于可视区域，所以top：0的效果就是导航栏刚好固定在绿色区域下方。
		// 难点3，如果非要让h5端显示正常，就需要给top的值赋值成uniapp文档中css语法上的变量--window--top了，意思为让可视区域距离顶部有跟小程序一样的距离25px，css中值为变量的写法需要借助var（），
		position: fixed;
		top: 0;
		/* uniapp封装的滚动条小程序中显示是没有默认样式即滚动条的，但在h5浏览器运行时是会出现默认样式滚动条的，uniapp封装的组件标签样式不能被修改，除非用穿透即/deep/ */
		/* 用穿透/deep/给uniapp封装好的组件标签改样式，让其在h5端运行时不会显示滚动条 */
		/* 有bug跟视频代码一模一样，效果却不一样，不搞了，小程序又不会出现默认样式 */

		.item {
			color: #333;
			/* 设置文字不会自动换行。因为100rpx就代表ipone4的屏幕最大宽度了，所以字体大小设置成40rpx，再加上设置内外边距，很容易导致文字比所在容器的宽度还宽，导致文字自动换行 */
			white-space: nowrap;
			font-size: 40rpx;
			/* padding的值有两个，那么第一个值就代表上下内边距，第二个值代表左右外边距 */
			/* 				padding: 0 10rpx;我选择直接在父元素中添加justify-content来代替该效果。 */
			padding: 0 30rpx;
			/* 设置行高，行高其实就是设置该元素的高度，区别在于行高减去字体高度剩下的高度会平均分配在字体上下 */
			line-height: 100rpx;

			// &是sass中的语法，代表上一级选择器选择的元素，例如此处的&.{}就代表item.{}
			// 下一行代码中的active仅仅是个class名。active鼠标点击后触发，visited鼠标点击前一刻。hover鼠标放上去时，使用方式为 元素：active，
			&.active {
				color: #31c27c;
			}
		}
	}

	.content {
		padding: 30rpx;
		padding-top: 100rpx;

		.row {
			/* 底部点虚线dotted */
			border-bottom: 1px dotted #efefef;
			padding: 20rpx 0;
		}
	}
	.loading{
		// 设置文本居中对齐
		text-align: center;
		fint-size:26rpx;
		color: #888;
		// 行高为字体大小的两倍
		line-height: 2em;
	}
</style>